.timeline ul{
    list-style-type:none;
    border-left:2px solid var(--clr-sun);
    padding:10px 5px;
}

.timeline.dark-mode ul{
  list-style-type:none;
  border-left:2px solid var(--clr-moon);
  padding:10px 5px;
}

.timeline ul li{
  padding:10px 10px;
  position:relative;
  transition:.5s;
}

.timeline ul li span{
  display:inline-block;
  background-color: #e0e0e0;
  border-radius:0.3rem;
  padding:2px 10px;
  font-size:15px;
  text-align:center;
}

.timeline.dark-mode ul li span{
  display:inline-block;
  background-color: #242424;
  border-radius:0.3rem;
  padding:2px 10px;
  font-size:15px;
  text-align:center;
}

.timeline ul li .content h3{
  color:#000000;
  font-size:17px;
  padding-top:5px;
}
.timeline ul li .content p{
  padding:5px 0px 15px 0px;
  font-size:13px;
}
.timeline ul li:before{
  position:absolute;
  content:'';
  width:10px;
  height:10px;
  background-color:#8a8a8a;
  border-radius:50%;
  left:-11px;
  top:20px;
  transition:.5s;
}
