:root {
    --bg-blur: #8181815e;
    --bg-modal: white;
    --bg-close: #8b8b8b;
    --bg-close-dark: #1b1b1b;
    --bg-x: #474747;
}

.modal-button {
    cursor:pointer;
    background-color: var(--bg-close);
    display: flex;
    min-width: 16px;
    color: #474747;
    border:none!important;
    border-radius: 0.1rem;
    transition: all 0.2s linear;
    align-content: center;
}

.modal-button.dark-mode {
    cursor:pointer;
    background-color: var(--bg-close-dark);
    display: flex;
    min-width: 16px;
    color: #474747;
    border:none!important;
    border-radius: 0.1em;
    transition: all 0.2s linear;
}

.modal-button:hover {
    transform: scale(1.02);
}

.modal-header {
    display: flex;
    align-items: center; /* Para alinhar verticalmente */
    position: relative;
    justify-content: center;
  }

.modal-controls {
    display: flex;
    top: 2.2px;
    right: 2.2px;
    position: absolute;
    justify-content: center;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--bg-blur);
    z-index: 1;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: black;
    padding: 1rem;
    border-radius: 0.3rem;
    width: 96%;
}