.block-top {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    z-index: 1;
}

.main>.inner-top {
    display: flex;
    justify-content: center;
    max-width: 100%;
    background-color: var(--clr-background-light);
    border-radius: 0rem 0rem 0.50rem 0.5rem;
    transition: background-color 0.2s ease-in-out;
    width: var(--width);
    position: relative;
}

.main>.inner-top.dark-mode {
    justify-content: left;
    justify-content: center;
    max-width: 100%;
    background-color: var(--clr-background-dark);
    transition: background-color 0.2s ease-in-out;
    width: var(--width);
    position: relative;
}

