    transition: transform 0.25s ease, filter 0.25s ease;
    padding: 5%;
    background-color: var(--clr-block-highlight-dark);
    border-radius: var(--border-radius-tl) var(--border-radius-tr) var(--border-radius-br) var(--border-radius-bl);
