html {
    scroll-behavior: smooth;
  }

.frame-pbi {
    position: relative;
    width: 100%;
    padding-bottom: 45%; /* Proporção 16:9 com altura menor (9 dividido por 16 vezes 100%) */
  }
  
.frame-pbi>iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }


.image {
    display: block;
    line-height: 0;
    max-width: 100%;
    position: relative;
}

.image .frame {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    display: inline-block;
    max-width: 100%;
    overflow: hidden;
    vertical-align: top;
    width: 100%;
}

.image .frame img {
    border-radius: 0 !important;
    max-width: 100%;
    vertical-align: top;
    width: inherit;
}

.image.full .frame {
    display: block;
}

.image.full:first-child .frame {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
}

.image.full:last-child .frame {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
}

.image.style1 {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.image.style1 .frame {
    width: 10.5rem;
    border-radius: 100%;
    border-color: var(--clr-sun);
    border-style: solid;
    border-width: 1px;
}

.image.style1 .frame.dark-mode {
    width: 10.5rem;
    border-radius: 100%;
    border-color: var(--clr-moon);
    border-style: solid;
    border-width: 1px;
}

.image.style1 img {
    border-radius: inherit !important;
    padding: 0.25rem;
}

.image.style1 .frame img {
    transition: transform 0.25s ease, filter 0.25s ease;
}

.image.style1 .frame:hover img {
    transform: scale(1.0425);
}

.image.style2 {
    text-align: center;
}

.image.style2 .frame {
    width: 4rem;
    border-radius: 100%;
    border-color: var(--clr-text);
    border-style: solid;
    border-width: 1px;
    box-shadow: 0rem 1.75rem 0rem 0rem transparent;
}

.image.style2 img {
    border-radius: inherit !important;
    padding: 0.625rem;
}

.image.style2 .frame img {
    transition: transform 0.25s ease, filter 0.25s ease;
}

.image.style2 .frame:hover img {
    transform: scale(1.0425);
}

