}

:root {
	--clr-background: #efefef;
	--clr-text: #333333;
	--clr-sun: #d69a00;
    --clr-moon: #1e84b8;

    --clr-toggle: #ebebeb;

    --clr-buttom: #cccccc;

    --clr-background-light: #FFFFFF;
    --clr-background-dark: #212121;

    --clr-bg-color: #e5e5e5;
    --clr-bg-color-dark: #1C1C1C;

    --clr-bg-highlight: #ebebeb;
    --clr-bg-highlight-dark: #272727;

    --clr-bg-highlight: #ebebeb;
    --clr-bg-highlight-dark: #272727;

    --clr-block-highlight: #f7f7f7;
    --clr-block-highlight-dark: #272727;

    --width: 42rem;

    --width-block:40rem;

    --text-strong:#66b5ff;

}

html[data-theme='dark'] {
	--clr-background: #333333;
	--clr-text: #efefef;
    --clr-buttom: #757575;
	--clr-toggle: #14587a;
}

/* ----- TOGGLE BUTTON | LAYOUT ----- */

.theme-switch {
	--diameter: 1.3rem;
	--spacing: 0.2rem;
	inline-size: calc(var(--diameter) * 2.2 + var(--spacing) * 2); /* width */
	block-size: calc(var(--diameter) + var(--spacing) * 2); /* height */
	border: none;
	cursor: pointer;
	background: var(--clr-toggle);
	color: var(--clr-text);
	position: relative;
	border-radius: 100vh;
	display: inline-flex;
	align-items: center;
	justify-content: space-around;
	transition:
		background-color 0.3s ease-in-out,
		color 0.3s ease-in-out;
}

.theme-switch[aria-pressed='true'] {
	--is-active: 1;
}

.theme-switch::before {
	--offset: calc(100% - var(--diameter) - var(--spacing) * 2);
	content: '';
	position: absolute;
	inset-inline-start: calc(var(--spacing) + var(--offset) * var(--is-active, 0)); /* left */
	inset-block-start: var(--spacing); /* top */
	border-radius: 100vh;
	inline-size: var(--diameter); /* width */
	block-size: var(--diameter); /* height */
	background: var(--clr-buttom);
	transition: inset-inline-start 0.3s ease-in-out; /* left */
}

.buttonEnd {
    padding: 0rem 2rem 0rem 2rem;
}

/* ----- TOGGLE BUTTON | ACCESSIBILITY ----- */

.no-js .theme-switch {
	display: none;
}

.theme-switch:focus {
	outline: 2px solid currentColor;
}

.theme-switch:focus:not(:focus-visible) {
	outline: 0;
}

hr {
    border: 0;
