}

.gallery ul li .thumbnail {
    border-radius: inherit;
    position: relative;
}

.gallery ul li .frame {
    -webkit-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    display: inline-block;
    overflow: hidden;
    vertical-align: top;
}

.gallery ul li img {
    margin: 0;
    max-width: 100%;
    vertical-align: top;
    width: auto;
}

.gallery ul li .caption {
    width: 100%;
}

.gallery ul li .caption p {
    width: 100%;
}

.gallery-modal {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    align-items: center;
    background-color: rgba(10, 10, 10, 0.85);
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    left: 0;
    opacity: 0;
    outline: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    transition: opacity 0.5s ease, visibility 0.5s, z-index 0.5s, background-color 0.5s ease;
    user-select: none;
    visibility: hidden;
    width: 100%;
}

.gallery-modal:before {
    animation: gallery-modal-spinner 1s infinite linear;
    background-image: url('data:image/svg+xml;base64,');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 4rem;
    content: '';
    display: block;
    height: 4rem;
    left: 50%;
    margin: -2rem 0 0 -2rem;
    opacity: 0;
    position: absolute;
    top: 50%;
    transition: opacity 0.25s ease;
    transition-delay: 0.5s;
    width: 4rem;
}

.gallery-modal .inner {
    opacity: 0;
    transform: translateY(0.75em);
    transition: all 0.5s ease;
}

.gallery-modal .inner img {
    box-shadow: 0 1em 3em 0 rgba(0, 0, 0, 0.35);
    display: block;
    max-height: 85vh;
    max-width: 80vw;
}

.gallery-modal .nav {
    background-image: url('data:image/svg+xml;base64,');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 3rem;
    cursor: pointer;
    height: 40%;
    max-width: 10vw;
    min-height: 6rem;
    opacity: 0.25;
    position: fixed;
    top: 30%;
    transition: opacity 0.25s ease, transform 0.5s ease;
    width: 8rem;
}

.gallery-modal .nav:hover {
    opacity: 1;
}

.gallery-modal .nav.next {
    right: 0;
}

.gallery-modal .nav.previous {
    left: 0;
    transform: scaleX(-1);
}

.gallery-modal .close {
    background-image: url('data:image/svg+xml;base64,');
    background-position: top 1rem right 1rem;
    background-repeat: no-repeat;
    background-size: 3rem;
    cursor: pointer;
    display: block;
    height: 6rem;
    opacity: 0.25;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity 0.25s ease, transform 0.5s ease;
    width: 6rem;
}

.gallery-modal .close:hover {
    opacity: 1;
}

.gallery-modal.visible {
    opacity: 1;
    pointer-events: auto;
    visibility: visible;
}

.gallery-modal.visible:before {
    opacity: 1;
}

.gallery-modal.loaded:before {
    opacity: 0 !important;
    transition-delay: 0s !important;
}

.gallery-modal.loaded .inner {
    opacity: 1;
    transform: translateY(0);
}

.gallery-modal.switching .inner {
    transition: opacity 0.375s ease, transform 0.5s ease;
}

.gallery-modal.zooming {
    background-color: rgba(0, 0, 0, 1);
}

.gallery-modal.zooming .nav.next {
    opacity: 0;
    pointer-events: none;
    transform: translateX(50%);
}

.gallery-modal.zooming .nav.previous {
    opacity: 0;
    pointer-events: none;
    transform: translateX(-50%) scaleX(-1);
}

.gallery-modal.zooming .close {
    opacity: 0;
    pointer-events: none;
    transform: translateX(50%) translateY(-50%);
}

